  "welcomeMailBody1_msg": "Estem contents que t'hagis registrat a Tutanota, i et donem la benvinguda a la teva bústia xifrada. Només tú pots desxifrar els teus missatges de correu electrònic i contactes amb la teva contrasenya. Si us plau, assegura't de no perdre la contrasenya; no podem restablir-la.",
  "welcomeMailBody2_msg": "Convida els teus amics a Tutanota, així podràs enviar missatges encriptats d´inici a fi fàcilment. També pots encriptar correus electrònics per a terceres persones amb un password. Tutatona encripta l´assumpte, el contingut i els arxius adjunts automàticament. Axó inclou també missatges que tu envïs o rebis sense ser encriptats, els nostres servidors en garanteixen l´encriptació. Suma't a la lluita per la privacitat i actualitza el teu compte a Premium per només 1 Euro al mes. Clica la opció Premium del menú principal una vegada dins del teu compte.",
